@keyframes introBG { from { transform: translate(-50%, -50%) rotate(15deg); }
  to { transform: translate(-50%, -50%) rotate(-15deg); } }

@keyframes slideInRight { from { transform: translate(100%, 0px); }
  to { transform: translate(0px, 0px); } }

@keyframes fadeIn { from { opacity: 0; }
  to { opacity: 1; } }

@keyframes fadeInUp { from { opacity: 0;
    transform: translate3d(0, 1rem, 0); }
  to { opacity: 1;
    transform: none; } }

@keyframes fadeInDown { from { opacity: 0;
    transform: translate3d(0, -1rem, 0); }
  to { opacity: 1;
    transform: none; } }

@keyframes fadeInUpIntro { from { opacity: 0;
    transform: translate3d(-50%, -45%, 0); }
  to { opacity: 1;
    transform: translate3d(-50%, -50%, 0); } }

@keyframes fadeInUpMoreArrow { from { opacity: 0;
    transform: translate3d(-50%, 200%, 0); }
  to { opacity: 1;
    transform: translate3d(-50%, 0px, 0); } }

html, body { font-size: 16px; font-family: "futura-pt", sans-serif; font-weight: 400; line-height: 1.2; }

h1, h2, h3, h4, h5, h6 { margin: 0px 0px 1rem; font-weight: 500; }

p { margin: 0px 0px 1.75rem; line-height: 1.4; }

p:last-child { margin-bottom: 0px; }

p strong, p b { font-weight: 500; }

.svg-dump { display: none; }

svg { box-sizing: content-box; }

.more-arrow { display: block; position: absolute; bottom: 1.5rem; left: 50%; transform: translate(-50%, 0px); min-width: 1.5rem; min-height: 1.5rem; width: 3vh; height: 3vh; }

* { box-sizing: border-box; }

.wrapper { margin: 0px 1.5rem; }

@media screen and (min-width: 64rem) { .wrapper { margin: 0px auto; width: 62rem; } }

article a { transition: color 400ms; }

article a:hover { color: #505050; }

body.nav-active { overflow: hidden; position: fixed; width: 100%; height: 100%; }

.nav-toggle { display: none; z-index: 199999; position: fixed; top: 1.25rem; right: 1.25rem; width: 2.5rem; height: 1.5625rem; overflow: hidden; }

.nav-toggle:hover { cursor: pointer; }

.nav-toggle:hover span { background: #505050; }

.nav-active .nav-toggle { overflow: visible; }

.nav-active .nav-toggle .closed { display: none; }

.nav-active .nav-toggle .open { display: block; position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); color: #fff; font-size: 5rem; line-height: 0.5; font-weight: 500; }

.nav-toggle .open { display: none; }

.nav-toggle .closed span { display: block; width: 100%; height: 0.1875rem; background: #000; transform: translate(100%, 0px); will-change: transform; animation-name: slideInRight; animation-duration: 400ms; animation-timing-function: ease-out; animation-fill-mode: forwards; }

.nav-has-toggled .nav-toggle .closed span { animation: none; transform: translate(0px, 0px); }

.nav-toggle .closed span + span { margin-top: 0.5rem; }

.nav-toggle .closed span:nth-child(1) { animation-delay: 4000ms; }

.nav-toggle .closed span:nth-child(2) { animation-delay: 4050ms; }

.nav-toggle .closed span:nth-child(3) { animation-delay: 4100ms; }

@media screen and (min-width: 64rem) { .nav-toggle { display: block; } }

nav { display: none; position: fixed; z-index: 99999; top: 0px; right: 0px; bottom: 0px; left: 0px; background: #000; text-transform: uppercase; text-align: center; font-size: 4vh; font-size: calc(var(--vh, 1vh) * 4); letter-spacing: 0.05em; }

.nav-active nav { display: block; }

nav a { color: #fff; font-weight: 700; text-decoration: none; }

nav .logo { display: block; margin: 0px auto; width: 6.7em; height: 1em; }

nav ul { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); width: 95%; list-style-type: none; margin: 0px; padding: 0px; }

nav ul li + li { margin-top: 2.5em; }

.intro { position: relative; padding: 3rem 0px; overflow: hidden; color: #000; }

.intro a { color: #000; transition: color 400ms; }

.intro a:hover { color: #505050; }

.intro .logo { display: block; margin: 0px auto; width: 16rem; height: 2.384rem; }

.intro .tagline { margin-top: 1rem; font-size: 1.5rem; text-align: center; }

.intro .letter { margin-top: 3rem; }

.intro .letter p { font-size: 1.125rem; }

.intro .letter .signature { text-align: right; font-weight: bold; }

.intro .more-arrow { display: none; }

@media screen and (min-width: 64rem) { .intro { height: 100vh; height: calc(var(--vh, 1vh) * 100); }
  .intro .content { position: absolute; top: 50%; left: 50%; transform: translate(-50%, -50%); margin: 0px auto; width: 50rem; max-width: 50rem; opacity: 0; animation-name: fadeIn; animation-delay: 500ms; animation-duration: 400ms; animation-timing-function: ease-out; animation-fill-mode: forwards; }
  .intro .more-arrow { opacity: 0; }
  .intro .logo { width: 30rem; height: 4.47rem; }
  .intro .tagline { font-size: 2rem; }
  .intro .letter { margin-top: 4rem; }
  .intro .letter p { font-size: 1.5rem; }
  .intro .more-arrow { display: block; animation-name: fadeInUpMoreArrow; animation-delay: 4000ms; animation-duration: 400ms; animation-timing-function: ease-out; animation-fill-mode: forwards; transition: color 400ms; }
  .intro .more-arrow:hover { cursor: pointer; padding-bottom: 0.125rem; color: #505050; } }

.work ul { list-style-type: none; margin: 0px; padding: 0px; }

.work .work-titles li { padding: 2rem; width: 100%; transition: width 200ms; font-weight: 500; letter-spacing: 0.05em; text-transform: uppercase; }

.work .work-titles li:hover { cursor: pointer; width: calc(100% + 0.5rem); }

.work .work-titles li:nth-child(odd) { background: #c8c8c8; }

.work .work-titles li:nth-child(even) { background: #b3b2b2; }

.work .work-titles li.active { width: calc(100% + 1rem); background: #3fcc68; }

.work .work-titles li a { color: #fff; text-decoration: none; }

.work .work-content li { display: none; padding: 2rem; }

.work .work-content li.active { display: block; }

@media screen and (min-width: 64rem) { .work { display: flex; flex-wrap: wrap; justify-content: space-between; height: 80vh; height: calc(var(--vh, 1vh) * 80); }
  .work .work-titles { width: 20%; }
  .work .work-titles ul { display: flex; flex-direction: column; align-items: stretch; height: 100%; }
  .work .work-titles ul li { position: relative; flex-basis: 100%; font-size: 1.5rem; }
  .work .work-titles ul li a { position: absolute; top: 50%; left: 2rem; transform: translate(0px, -50%); }
  .work .work-content { width: 75%; } }
